.graph{height: 600px;}
.graph svg text{fill: black !important;}


*{box-sizing: border-box;}
body{background-color: #f3f2ec}
header{
  width: 100%;
}
.nav{ 
  width: 80%; margin: 100px auto;
  display: flex; 
  align-items: center;
  justify-content: center;
  font-size: 20px;
}
.nav a{
  margin: 0 20px;
  color: rgb(151, 146, 129);
  text-decoration: none;
}
.nav a:hover{
  color:rgb(96, 128, 170);
}


section{
  text-align: center;
  margin-bottom: 10px;
}
section h2{
  text-align: center; 
  color: rgb(96, 128, 170);
  margin: 60px 0 20px;
}

table{margin: 0 auto;}
thead{}
thead tr{}
thead tr th{}

tr{padding: 20px;}
tr th{padding: 20px;}